<template>
    <!-- class="table table-hover table-dark" -->
    <div id="container">
        <button class="btn" @click="reload">Reload</button>
        <table>
            <thead>
                <tr>
                    <th scope="col">Order Date</th>
                    <th scope="col">Order Total</th>
                    <th scope="col">Payment Type</th>
                    <th scope="col">Table No.</th>
                    <th scope="col">Remove</th>
                </tr>
            </thead>
            <tbody id="myTable">
                <tr v-for="ord in allOrders" :key="ord._id">
                    <td>{{ ord.orderDate.substring(0, 10) }}</td>
                    <td>{{ ord.orderTotal }} ₹</td>
                    <td>{{ ord.paymentType }}</td>
                    <td>{{ ord.tableNumber }}</td>
                    <td><button class="btn-red" @click="remove(ord._id)">Remove</button></td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    name: 'historyComp',
    data() {
        return {
            allOrders: [],
            HOST: window.location.host,
            PROTOCOL: window.location.protocol,
        }
    },
    created() {
        fetch(`${this.PROTOCOL}//${this.HOST}/orders/fetchAll`)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                }
            }).then((data) => {
                this.allOrders = data;
            })
    },
    methods: {
        reload() {
            location.reload();
        },
        remove(id) {
            fetch(`${this.PROTOCOL}//${this.HOST}/orders/removeFromAll` + id, {
                method: 'DELETE'
            });
            location.reload();
        }
    }
}
</script>
<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

* {
    font-family: 'Poppins', sans-serif;
}

#container {
    background-color: rgba(3, 3, 2, 0.788);
    position: absolute;
    top: 5%;
    left: 17%;
    border: 2px solid rgba(0, 0, 0, 0.897);
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 2%;
    width: 80%;
    padding: 15px;
    flex-direction: column;
}

.btn {
    width: 7vw;
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #ffffff;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 2vw 0.3vw 2vw;
    margin: 0.9vw 2vw 0.4vw 0vw;
}

.btn:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #28ac84;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn:hover {
    background-color: #abfc50;
    color: rgb(17, 13, 13);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: scale(1.1);
}

.btn:hover::before {
    transform: skewX(30deg) translate(300%, -50%);
}

.btn:active {
    background: rgb(135, 204, 175);
    transform: scale(0.9);
}

.btn-red {
    background-color: #f37171;
    border-bottom: 2px solid #093A3E;
    font-size: 12px;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 1vw 0.2vw 1vw 0.2vw;
}

.btn-red:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #7c1a13;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-red:hover {
    background-color: #df1b15c9;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-red:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

thead {
    color: rgb(255, 255, 255);
    letter-spacing: 0.12vw;
    border: 1px solid wheat;
}

th {
    border-bottom: 1px solid wheat;
    padding: 0.4vw 0.1vw 0.4vw 0.5vw;
    text-align: center;
}

td {
    text-align: center;
    color: rgb(255, 255, 255);
    border-bottom: 1px solid wheat;
    margin: 10px;
}
</style>