<template>
    <div id="container">
        <div class="no" v-if="orders.length < 1">
            <span>NO CURRENT ORDERS !</span>
            <img src="../assets/taxi-no-connection.gif" alt="">
        </div>
        <div class="card" v-for="i in orders" :key="i.isServed">
            <div class="card-body">

                <!-- Card Header Part -->
                <div class="card-header">
                    <span id="tableNumber">{{ i.tableNumber }}</span>
                    <div style="margin:0vw 0vw 1vw 0vw;position:relative;left:2vw;bottom:0.7vw">
                        <label v-if="i.isServed == false" style="color:tomato">Not served yet </label>
                        <label>Payment Type: {{ i.paymentType }}</label>
                        <div v-if="i.isPaymentDone == true">
                            <label style="color: #21d667;"> <img style="width:2vw" src="../assets/done.png"
                                    alt="">&nbsp;Payment Done</label>
                        </div>
                        <div>
                            <label>Total: {{ i.total }} ₹</label>
                        </div>
                    </div>
                </div>
                <div class="line"></div>
                <!-- Order items is visible here -->
                <ul v-if="!i.isServed">
                    <li v-for="j in i.order" :key="j.name">
                        ➭ {{ j.name }}
                    </li>
                </ul>

                <!-- Buttons are visible Here -->
                <div v-if="i.disableButtons == false">
                    <button class="btn-green"
                        @click="confirm_recived_order(i._id, i.tableNumber, i.paymentType, i.total)">Confirm</button>
                    <button class="btn-red" @click="cancle_recived_order(i._id)">Cancel</button>
                </div>
                <div v-if="i.isServed == true" class="served">
                    <label>Order Served</label>
                    <div>
                        <button class="btn-red" @click="remove_order(i._id)"
                            style="margin:1vw 0vw 0vw -0.2vw">Remove</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ordersComponent',
    data() {
        return {
            orders: [],
            HOST: window.location.host,
            PROTOCOL: window.location.protocol,
        }
    },
    created() {
        setInterval(this.get_orders, 1000);
    },
    methods: {
        get_orders() {
            fetch(`${this.PROTOCOL}//${this.HOST}/orders/fetchOrders`)
                .then((response) => {
                    if (response.ok) {
                        return response.json();
                    }
                    else {
                        console.log("error");
                    }
                }).then((data) => {
                    this.orders = data;
                })
        },
        confirm_recived_order(id, tableNumber, paymentType, orderTotal) {
            fetch(`${this.PROTOCOL}//${this.HOST}/orders/updatePaymentStatus` + id, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    isPaymentDone: true,
                    disableButtons: true
                })
            });
            fetch(`${this.PROTOCOL}//${this.HOST}/orders/saveToAllData`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    tableNumber: tableNumber,
                    orderTotal: orderTotal,
                    paymentType: paymentType
                })
            });
        },
        cancle_recived_order(id) {
            fetch(`${this.PROTOCOL}//${this.HOST}/orders/cancleThisOrder` + id, {
                method: 'DELETE'
            });
        },
        remove_order(id) {
            fetch(`${this.PROTOCOL}//${this.HOST}/orders/orderServed` + id, {
                method: 'DELETE'
            });
        }
    }
}
</script>
<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

* {
    font-family: 'Poppins', sans-serif;
}

#container {
    display: flex;
    align-items: flex-start;
    position: relative;
    left: 35vh;
    top: 5vh;
    width: 165vh;
    padding: 2vh 0vh 0vh 6vh;
    color: white;
    overflow-y: auto;
    flex-wrap: wrap;
    gap: 20px;
    height: 100%;
    background-color: rgba(3, 3, 2, 0.671);
    border: 2px solid rgba(0, 0, 0, 0.897);
    border-radius: 10px;
}

.card {
    color: black;
    height: 100%;
    max-width: 40vh;
    background-color: antiquewhite;
    flex: 1 0 200px;
}

.card:hover {
    transform: scale(1.02);
    transition: 0.2s all;
    border: 3px solid rgb(138, 102, 78);
}

.card {
    background-color: rgba(3, 3, 2, 0.788);
    margin: 1%;
    height: 30%;
    max-width: 30vw;
    display: flex;
    flex-direction: column;
    flex: 1 1 25%;
    flex-wrap: wrap;
    border: 2px solid rgb(117, 56, 15);
    transition: 0.2s all;
    border-radius: 1.5vw;
    flex-direction: column;
}

#tableNumber {
    color: wheat;
    font-size: 2vw;
    font-weight: bolder;
    border: 4px solid rgb(175, 175, 175);
    padding: 0.5vw 1.5vw 0.5vw 1.5vw;
    border-radius: 10vw;
    height: 4.8vw;
    background: rgba(245, 222, 179, 0.11);
    position: relative;
    bottom: 0.9vw;
    right: 1.4vw;
}

.line {
    background: white;
    height: 0.05vw;
    width: 15vw;
    position: relative;
    bottom: 0.7vw;
    right: 0.7vw;
}

.card-header {

    display: flex;
    flex-direction: row;
}

.card-header label {
    color: wheat;
    margin-left: 4vw;
    letter-spacing: 0.1vw;
}

ul {
    list-style: none;
    color: wheat;
    letter-spacing: 0.1vw;
    margin-left: -2vw;
}

/* Button red  */
.btn-red {
    background: none;
    border-bottom: 2px solid #4d0505;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #f37171;
    font-weight: bolder;
    box-shadow: 0 0.5px 0 0.5px rgb(24, 10, 10);
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 1vw 0vw 0.8vw;
    letter-spacing: 0.1vw;
    width: 7vw;
}

.btn-red:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #7c1a13;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-red:hover {
    background-color: #df1b15c9;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-red:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-red:active {
    transform: scale(0.9);
}

/* Button green */
.btn-green {
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #fff6dc;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 0.2vw 0vw 0.2vw;
    width: 7vw;
}

.btn-green:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #0c4107;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-green:hover {
    background-color: #1c5833;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-green:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-green:active {
    transform: scale(0.9);
}

.served label {
    letter-spacing: 0.1vw;
    color: #21d667;
    font-weight: 400;
    font-size: 1.4vw;
}

.no {
    position: relative;
    left: 12vw;
    display: flex;
    flex-direction: column;
}

.no img {
    mix-blend-mode: color-burn;
}

.no span {
    font-size: 4vw;
    color: rgb(245, 240, 220);
    letter-spacing: 0.4vw;
    animation: span-animation 2s infinite alternate linear;
    padding: 1vw 3vw 0vw 3vw;
}

@keyframes span-animation {
    from {
        color: beige;
    }

    to {
        transform: scale(0.95);
        color: rgb(255, 238, 145);
    }
}
</style>