<template>
    <div id="navbar">
        <div class="nav-item btn" :class="active[4] ? 'active' : pass"  @click="this.$router.push('/'),this.active[4]=1,changeState(4)">Home</div>
        <div id="orders" class="nav-item btn" :class="active[0] ? 'active' : pass"  @click="this.$router.push('/orders'),this.active[0]=1,changeState(0)">Orders</div>
        <div id="history" class="nav-item btn" :class="active[1] ? 'active' : pass"  @click="this.$router.push('/history'),this.active[1]=1,changeState(1)">History</div>
        <div id="addNew" class="nav-item btn" :class="active[2] ? 'active' : pass"  @click="this.$router.push('/addNew'),this.active[2]=1,changeState(2)">New Item</div>
        <div id="viewAll" class="nav-item btn" :class="active[3] ? 'active' : pass"  @click="this.$router.push('/viewAll'),this.active[3]=1,changeState(3)">View All</div>
    </div>
</template>

<script>
export default {
    name: "navBar",
    data(){
        return{
            active:[0,0,0,0,1]
        }
    },
    methods:{
        changeState(i){
            for (let index = 0; index < this.active.length; index++) {
                if(index == i){
                    //Notthing
                }
                else{
                    this.active[index] = 0
                }
                
            }
        }
    }
}
</script>
<style scoped>
/* For item names */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
#navbar{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: fixed;
    top: 25%;
    margin: 2% 2% 2% 0%;
    padding: 1%;
    width: 15%;
}
.btn {
    width: 10rem;
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 20px;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #ffffff;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 2vw 0.3vw 2vw;
    margin: 0.9vw 2vw 0.4vw 2vw;
}

.btn:before {
 content: "";
 position: absolute;
 width: 100px;
 height: 120%;
 background-color: #28ac84;
 top: 50%;
 transform: skewX(30deg) translate(-150%, -50%);
 transition: all 0.5s;
}

.btn:hover {
 background-color: #e9be7e;
 color: rgb(17, 13, 13);
 box-shadow: 0 1px 0 1px rgb(0, 0, 0);
 transform:scale(1.1);
}
.btn:hover::before {
 transform: skewX(30deg) translate(300%, -50%);
}

.btn:active {
 transform: scale(0.9);
}
.active{
    background: rgb(165, 110, 59);
    transform: scale(0.9);
    border: 2px solid white;
}
</style>