import { createApp } from 'vue'
import { createRouter, createWebHistory } from 'vue-router'
import App from './App.vue'

import addNewItem from './components/addnewitem-component.vue'
import viewAll from './components/viewall-component.vue'
import history from './components/history-component.vue'
import ordersComponent from './components/orders-component.vue'
import homeComponent from './components/home-component.vue'
const router = createRouter({
    history: createWebHistory(),
    routes:[
        {path:'/addNew',component:addNewItem},
        {path:'/viewAll',component:viewAll},
        {path:'/history',component:history},
        {path:'/orders',component:ordersComponent},
        {path:'/',component:homeComponent}
    ]   
});

const app = createApp(App);

app.use(router);

app.mount("#app")