<template>
    <div style="position:relative;left:17vw;margin:0.3vw 0vw 0.3vw 0vw;">
        <input class="input" style="width: 60vw;" type="text" placeholder="Search Item" v-model="search">
    </div>
    <div id="container">
        <div class="card" v-for="item in this.fetchedItems" :key="item.itemName" style="width: 18rem">
            <div v-if="!item.update">
                <h5>{{ item.itemName }}</h5>
                <h6>{{ item.itemPrice }} ₹</h6>
                <p>{{ item.itemType }}</p>&nbsp;
                <div v-if="item.isSpicy || item.isSpecial">
                    <img class="chilly" src="../assets/spicy.png" alt="(spicy)" v-if="item.isSpicy">
                    <span v-if="item.isSpecial" class="special"><img src="../assets/premium-quality.png" alt=""></span>
                </div>
                <div>
                    <ul>
                        <li v-for="i in item.itemIngredients" :key="i">{{ i }}</li>
                    </ul>
                </div>
                <button v-if="!item.delete" class="btn-blue"
                    @click="btnClick(0, item.itemName, item.itemPrice, item.itemType, item.isSpecial, item.isSpicy, item.itemIngredients), item.update = !item.update">Edit
                </button>
                <button @click="item.delete = !item.delete" v-if="!item.delete" class="btn-red">Delete</button>
                <div v-if="item.delete">
                    <label style="color:beige;margin-right:1vw;letter-spacing:0.1vw">Confirm ?</label>
                    <button @click="deleteItem(item.id)" class="btn-green">Yes</button>
                    <button @click="item.delete = !item.delete" class="btn-red">No</button>
                </div>
            </div>
            <div v-if="item.update">
                <div class="card-body">
                    <input class="input" type="text" v-model="updatedName" placeholder="Name">
                    <input type="Number" class="input" v-model="updatedPrice" placeholder="Price">
                    <select class="input" v-model="updatedType">
                        <option class="input" value="Starter">Starter</option>
                        <option class="input" value="Main course">Main course</option>
                        <option class="input" value="Beverage">Beverage</option>
                        <option class="input" value="Desert">Desert</option>
                    </select>
                    <div id="checkboxes">
                        <input class="form-check-input" type="checkbox" v-model="updatedSpecial" id="special"
                            style="margin-right:0.7vw">
                        <label class="checkmark" for="special" style="margin-right:1.5vw;color:beige">Special</label>
                        <input class="form-check-input" v-model="updatedSpicy" id="spicy" style="margin-right:0.7vw"
                            type="checkbox">
                        <label class="form-check-label" for="spicy" style="color:beige">Spicy</label>
                    </div>
                    <div>
                        <ul>
                            <li v-for="li in updatedList" :key="li">{{ li }} <button class="btn-red"
                                    style="width: 7vw;margin-top:1vw" @click="remove_from_updated_list(li)">Remove</button>
                            </li>
                        </ul>
                        <input type="text" class="input" v-model="temp" placeholder="Add new ingredient and press enter"
                            v-on:keyup.enter="check_for_enter">
                        <label v-if="ingreError == true" style="color:red;font-weight:bold;margin:1vw 0vw 0vw 1vw">Cannot be
                            null</label>
                    </div><br>
                    <div>
                        <button @click="item.update = !item.update, updateItem(item.id)" class="btn-green">save</button>
                        <button @click="btnClick(1), item.update = !item.update" class="btn-red">cancel</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "viewAll",
    data() {
        return {
            fetchedItems: [],
            updatedName: "",
            updatedPrice: "",
            updatedType: "Starter",
            updatedSpecial: false,
            updatedSpicy: false,
            updatedList: [],
            temp: '',
            ingreError: false,
            allMenuItems: [],
            search: '',
            HOST: window.location.host,
            PROTOCOL: window.location.protocol,
        }
    },
    watch: {
        search() {
            this.search_item();
        }
    },
    methods: {
        search_item() {
            console.log(this.search)
            if (this.search.trim() == "") {
                this.fetchedItems = this.allMenuItems
            }
            this.fetchedItems = this.allMenuItems.filter(item => {
                if (item.itemName.toLowerCase().indexOf(this.search.toLowerCase()) != -1) {
                    return true
                }
            })
        },
        check_for_enter(e) {

            if (e.key == 'Enter') {
                if (this.temp.trim() == "") {
                    this.ingreError = true;
                }
                else {
                    let temp = this.temp[0].toUpperCase() + this.temp.slice(1);
                    this.updatedList.push(temp);
                    this.temp = "";
                    this.ingreError = false;
                }
            }
        },
        remove_from_updated_list(i) {
            let index = this.updatedList.indexOf(i);
            if (index != -1) {
                this.updatedList.splice(index, 1);
            }
        },
        updateItem(id) {
            fetch(`${this.PROTOCOL}//${this.HOST}/getFoodItems/` + id, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    itemName: this.updatedName,
                    itemPrice: this.updatedPrice,
                    itemType: this.updatedType,
                    isSpecial: this.updatedSpecial,
                    isSpicy: this.updatedSpicy,
                    ingredients: this.updatedList
                })
            });
            location.reload();
        },
        // Fetch Items here
        fetchItems() {
            fetch(`${this.PROTOCOL}//${this.HOST}/getFoodItems`)
                .then((response) => {
                    if (response.ok) {
                        return response.json();
                    }
                }).then((data) => {
                    const results = [];
                    for (const id in data) {
                        results.push({
                            id: data[id]._id,
                            isSpecial: data[id].isSpecial,
                            isSpicy: data[id].isSpicy,
                            itemName: data[id].itemName,
                            itemPrice: data[id].itemPrice,
                            itemType: data[id].itemType,
                            itemIngredients: data[id].ingredients,
                            update: false,
                            delete: false
                        })
                    }
                    this.fetchedItems = results.slice(0);
                    this.allMenuItems = this.fetchedItems.slice(0)

                })
        },
        btnClick(a, n, p, t, s, sp, il) {
            if (a == 0) {
                this.updatedName = n;
                this.updatedPrice = p;
                this.updatedType = t;
                this.updatedSpecial = s;
                this.updatedSpicy = sp;
                this.updatedList = il.slice(0);
            }
            else {
                this.updatedName = "";
                this.updatedPrice = "";
                this.updatedType = "";
                this.updatedSpecial = 0;
                this.updatedSpicy = 0;
                this.updatedList = []
            }
        },
        deleteItem(id) {
            fetch(`${this.PROTOCOL}//${this.HOST}/getFoodItems/` + id, {
                method: 'DELETE'
            });
            location.reload();
        }
    },
    created() {
        this.fetchItems();
    }
}
</script>
<style scoped>
