    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 2%;
}

.card {
    background-color: rgba(3, 3, 2, 0.788);
    margin: 1%;
    padding: 1%;
    height: 30%;
    max-width: 30vw;
    display: flex;
    flex-direction: column;
    flex: 1 1 25%;
    flex-wrap: wrap;
    border: 2px solid rgb(117, 56, 15);
    transition: 0.2s all;
    border-radius: 1.5vw;
    padding: 1vw;
}

h5,
h6,
p {
    color: rgb(182, 179, 179);
    letter-spacing: 0.1vw;
}

h5 {
    font-weight: 700;
    border-bottom: 1px solid rgb(99, 99, 99);
    padding-bottom: .65vw;
    font-size: 1.5vw;
}

h6 {
    font-weight: 600;
    font-size: 1.3vw;
}

p {
    font-size: 1.1vw;
}

.card:hover {
    transform: scale(1.02);
    transition: 0.2s all;
    border: 3px solid rgb(138, 102, 78);
}

.chilly {
    width: 2.2vw;
    margin-bottom: 1vw;
    margin-right: 1vw;
}

.special img {
    width: 2.5vw;
    animation: sp 8s infinite alternate linear;
    margin-bottom: 1vw;
}

.special img:hover {
    animation-play-state: paused;
}

@keyframes sp {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.chilly {
    animation: ch 3s infinite alternate linear;
    border-radius: 100%;
}

@keyframes ch {
    from {
        box-shadow: rgba(255, 6, 6, 0.2) 0px 54px 55px, rgba(250, 0, 0, 0.2) 0px -12px 30px, rgba(255, 0, 0, 0.2) 0px 4px 6px, rgba(255, 0, 0, 0.2) 0px 12px 13px, rgba(255, 0, 0, 0.2) 0px -3px 5px;
    }

    to {
        box-shadow: rgba(255, 6, 6, 0) 0px 54px 55px, rgba(250, 0, 0, 0) 0px -12px 30px, rgba(255, 0, 0, 0) 0px 4px 6px, rgba(255, 0, 0, 0) 0px 12px 13px, rgba(255, 0, 0, 0) 0px -3px 5px;
    }
}

ul {
    margin: 0.5vw 1vw 1.5vw -1.5vw;
}

li {
    color: rgb(228, 166, 125);
    letter-spacing: 0.15vw;
    font-size: 1.4vw;
}

/* Button */
.btn-blue {

    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #8faaa6;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 2vw 0.3vw 2vw;
    letter-spacing: 0.1vw;
}

.btn-blue:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #674c86;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-blue:hover {
    background-color: #639df3;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-blue:hover::before {
    transform: skewX(30deg) translate(300%, -50%);
}

.btn-blue:active {
    transform: scale(0.9);
}

/* Button red  */
.btn-red {
    background: none;
    border-bottom: 2px solid #4d0505;
