    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 1vw 0vw 0.8vw;
    letter-spacing: 0.1vw;
    width: 6.4vw;
}

.btn-red:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #7c1a13;
    top: 50%;
