    transition: all 0.5s;
}

.btn-red:hover {
    background-color: #df1b15c9;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-red:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-red:active {
    transform: scale(0.9);
}

/* Button green */
.btn-green {
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #fff6dc;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 0.2vw 0vw 0.2vw;
    width: 6vw;
}

.btn-green:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #0c4107;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
